//vuex
import Vue from 'vue';
import Vuex from  'vuex';
import mutations from './mutation';
import actions from './actions'
//
Vue.use(Vuex);

//Store
const store = new Vuex.Store({
//
  state:{
    //
    topics:[
      {name:'all',desc:''},
      {name:'good',desc:''},
      {name:'share',desc:''},
      {name:'ask',desc:''},
      {name:'job',desc:''},
      {name:'dev',desc:''},
    ],
    //
    unLoginedMoreMenus:{
      login:'',
    },
    //
    loginedMoreMenus:{
      create:'',
      center:'',
      msg:'',
      logout:''
    },
    //
    isLogined:false,
    //
    loginname:'',
    //accesstoken
    accesstoken:'',
    //id
    author_id:'',
    //
    isShowMoreMenus:false

  },
  //
  getters:{
    moreMenus:function(state){
      if (state.isLogined){
        //
        return state.loginedMoreMenus;
      }
      return state.unLoginedMoreMenus;
    },
    getTopicDescByTab:function (state) {
        //
      return function (tab) {
      for (let i = 0;i < state.topics.length;i++){
        if (state.topics[i].name == tab){
          return state.topics[i].desc;
        }
      }
      }

    }
  },
  mutations,
  actions
});

//
export default store;



